body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  background-color: #000;
}

.app {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  font-family: Lato, sans-serif;
  padding-top: 15px;
}

.contenedor-logo {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 20px;
}
.contenedor-counter{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.logo-clickcounter {
  height: 50px;
}
button{
    width :250px;
    height:80px;
    font-family: roboto, sans-serif;
    border-radius: 15px;
    font-weight: bold;
    color: #FFF;
    border: 2px solid #FFF;
    margin :5px;
    cursor: pointer;
    font-size: 40px;
}
button:hover{
    background-color: #0b7;
}
.boton-click{
    background-color: #02F;
}
.boton-reiniciar{
    background-color: #507;
}
.contador{
  width: 300px;
  height :300px;
  display:flex;
  justify-content: center;
  align-items: center;
  font-size: 150px;
  border: 8px solid #FFF;
  margin-bottom: 20px;
  color: #fff;
}
